function Image ({ image, name, slug, description, stack, index }) {
  const [hover, setHover] = useState({
    root: false,
    top: false,
    bottom: false
  })
  const isActive = () => {
    return hover.root || hover.top || hover.bottom
  }

  return (
    <article key={index} className='project-showcase__project'>
      <div
        role='presentation'
        onMouseEnter={() => {
          setHover({ ...hover, root: true })
        }}
        onMouseLeave={() => {
          setHover({ bottom: false, root: false, top: false })
        }}
        className='project-showcase__project_image-con'
      ></div>
 
      <GatsbyImage image={image} alt={''} />
      {/* the element that appears on hover */}
      <div
        role='presentation'
        onMouseEnter={() => {
          setHover({ ...hover, top: true })
        }}
        onMouseLeave={() => {
          setHover({ bottom: false, root: false, top: false })
        }}
        className={
          'project-showcase__project__on-hover top' +
          (isActive() ? ' active' : '')
        }
      >
        <div className={'project-showcase__project_on-hover-top__text-content'}>
          <span>{name}</span>
          <p>{description}</p>
        </div>
      </div>
      <div
        role='presentation'
        onMouseEnter={() => {
          setHover({ ...hover, bottom: true })
        }}
        onMouseLeave={() => {
          setHover({ bottom: false, root: false, top: false })
        }}
        className={
          'project-showcase__project__on-hover bottom' +
          (isActive() ? ' active' : '')
        }
      >
        <div className='project-showcase__project__on-hover__stack_con'>
          {stack.map((item, index) => {
            return (
              <Stack
                //use the name to give the toolip a unique id
                parentProject={name}
                id={index}
                title={item.title}
                src={item.src}
                key={index + '_stack_' + item.title}
              />
            )
          })}
        </div>
      </div>
    </article>
  )
}
export default function ProjectShowcase ({ sectionRef }) {
  const data = useStaticQuery(graphql`
    query HeaderQuery {
      allFile(filter: { relativeDirectory: { eq: "thumbnails" } }) {
        edges {
          node {
            id
            childImageSharp {
              gatsbyImageData(
                layout: CONSTRAINED
                placeholder: BLURRED
                width: 1240
              )
            }
            base
          }
        }
      }
    }
  `)

  function getGatsbyImageData (name) {
    for (const item of data.allFile.edges) {
      if (item.node.base === name) return getImage(item.node)
    }
  }
  return (
    <Container bgColor='dark'>
      <section
        ref={sectionRef}
        id='portfolio'
        className='section-spacing project-showcase'
      >
        <SectionHeader title='Project Showcase' />
        <Masonry
          defaultColumns={2}
          columns={{ xs: 1, sm: 1, md: 2 }}
          defaultSpacing={2}
          spacing={{ xs: 1, sm: 1, md: 2 }}
        >
          {prjs.map((prjItem, index) => {
            const { image, name, description, stack, slug } = prjItem

            return (
              <Image
                image={getImage(getGatsbyImageData(image))}
                name={name}
                slug={slug}
                description={description}
                stack={stack}
                key={index}
              />
            )
          })}
        </Masonry>
      </section>
    </Container>
  )
}
