import React from 'react'
import SectionHeader from './SectionHeader'
import WorkExpCard from './WorkExpCard'
import { graphql, StaticQuery } from 'gatsby'
import Container from './Container'
export default function JobHistory ({ sectionRef }) {
  //in the query the data field is called content because of type conflict with the other json files (data: {}) instead of our data now content (data: [])
  return (
    <StaticQuery
      query={graphql`
        query jobs {
          json(type: { eq: "work-timeline" }) {
            id
            slug
            content {
              duration
              position
              company
              description
            }
          }
        }
      `}
      render={data => {
        const jobsArr = data.json.content
        return (
          <Container bgColor='dark'>
            <section
              ref={sectionRef}
              id='job-history'
              className='section-spacing '
            >
              <SectionHeader title='Working Experience' />
              <div className='job-history-card-container'>
                {jobsArr.map((item, index) => {
                  return <WorkExpCard key={index} {...item} />
                })}
              </div>
            </section>
          </Container>
        )
      }}
    />
  )
}
