import React, { useEffect, useState } from 'react'
import Tooltip, { TooltipProps, tooltipClasses } from '@mui/material/Tooltip'
import { styled } from '@mui/material/styles'

const defs = [
  {
    title: 'react native',
    def:
      'React native is a cross-platform tool for developing native mobile applications'
  },
  {
    title: 'javascript',
    def: 'JavaScript is the programming language of the Web'
  },
  {
    title: 'redux',
    def: 'Redux is a state management tool'
  },
  {
    title: 'html',
    def: 'HTML is the standard markup language for Web pages'
  },
  {
    title: 'css',
    def: 'CSS is the standard tool for styling web pages'
  },
  {
    title: 'sass',
    def: 'Sass powerful professional grade CSS extension language'
  }
]
const CustomTooltip = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: 'var(--d-black)',
    color: 'var(--cream)',
    fontSize: 16,
    fontWeight: 600,
    padding: '8px 12px',
    lineHeight: 1.25,
    boxShadow: '0 1px 5px var(--d-purple)'
  }
}))

export default function Stack ({
  title,
  src,
  containerStyle,
  id,
  parentProject
}) {
  const [tooltipTitle, setTooltipTitle] = useState<String>('')
  useEffect(() => {
    setTooltipTitle(getDef(title))
  }, [])
  const getDef = defTitle => {
    defTitle = defTitle.toLowerCase()
    let def = defs.filter(item => {
      if (item.title === defTitle) {
        return true
      }
      return false
    })
    if (def.length > 0) return def[0].def
    else return ''
  }
  const identifier = 'stack_tooltip_' + parentProject + '_' + id
  return (
    <CustomTooltip
      id={identifier}
      title={
        <>
          <p>{tooltipTitle}</p>
        </>
      }
    >
      <p
        data-for={identifier}
        data-tip
        className='stack-item'
        style={containerStyle}
      >
        <img height='20px' width='20px' src={src} alt={''} />
        <span>{title}</span>
      </p>
    </CustomTooltip>
  )
}
