import React from 'react'

interface Props {
  duration: string
  position: string
  company: string
  description: string
}

export default function WorkExpCard ({
  duration = '',
  position = '',
  company = '',
  description = ''
}: Props) {
  return (
    <article className='workexp-element'>
      <div className='workexp-element__title'>
        <span className='workexp-element__title__title'>{position}</span>
        <span className='workexp-element__title__company'>{company}</span>
      </div>
      <p className='workexp-element__duration'>{duration}</p>
      <div className='workexp-element__divider'></div>
      <p className='workexp-element__description'>{description}</p>
    </article>
  )
}
