import React from 'react'

export default function ServiceCard ({ title, description, image }) {
  return (
    <article
      aria-label={'service: ' + title + ' ' + description}
      className='services__con__card'
    >
      <div className='services__con__card__header'>
        <img
          src={image}
          style={{ height: 150, width: 150, alignSelf: 'center' }}
          alt={'icon'}
        />
        <p className='services__con__card__header__title'> {title}</p>
      </div>
      <p className='services__con__card__description'>{description}</p>
    </article> 
  )
}
