import React from 'react'
import SectionHeader from './SectionHeader'
import ServiceCard from './ServiceCard'
import Container from "./Container"
import { graphql, useStaticQuery } from 'gatsby'

export default function Services ({ sectionRef }) {
  const queryData = useStaticQuery(graphql`
    query services {
      json(type: { eq: "services" }) {
        id
        slug
        content {
          title
          description
          image
        }
      }
    }
  `)
  const serviceArray = queryData.json.content
  return (
    
    <Container bgColor='light'>
      <section ref={sectionRef} id='services' className='section-spacing '>
        <SectionHeader title='Services' />
        <div className='services__con'>
          {serviceArray.map((item, index) => {
            const { title, description, image } = item
            return (
              <ServiceCard
                key={index}
                title={title}
                description={description}
                image={image}
              />
            )
          })}
        </div>
      </section>
    </Container>
  )
}
