import { graphql, StaticQuery } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import React from 'react'
import Button from './Button'
import Container from './container'
/*
import static queries like these directly from here 
not through index imports
*/
export default function CallToAction ({ sectionRef, contactMeRef }) {
  return (
    <StaticQuery
      query={graphql`
        query {
          file(relativePath: { eq: "house.jpg" }) {
            childImageSharp {
              gatsbyImageData(
                layout: CONSTRAINED
                placeholder: BLURRED
                width: 1240
              )
            }
          }
          json(type: { eq: "call-to-action" }) {
            id
            slug
            header
            subHeader
          }
        }
      `}
      render={data => {
        const { header, subHeader } = data.json

        return (
          <Container bgColor='light'>
            <section id='home' ref={sectionRef} className='call-to-action'>
              <div className='call-to-action__content'>
                <h1 className='call-to-action__content__header'>{header}</h1>
                <p className='call-to-action__content__para'>{subHeader}</p>
                <Button
                  text='Contact me'
                  onPress={() => {
                    contactMeRef.current.scrollIntoView()
                  }}
                />
              </div>
              <div className='call-to-action__picture'>
                <GatsbyImage
                  image={getImage(data.file)}
                  alt={'call to action picture, picture of the dev'}
                />
              </div>
            </section>
          </Container>
        )
      }}
    />
  )
}
